import { inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../environments/environment';
import { NoiDungLienHeModel, NoiDungLienHeModelReq } from '../../../../models/noi-dung-lien-he.model';
import { env } from 'process';
import { id } from '@swimlane/ngx-charts';

@Injectable({ providedIn: 'root' })
export class TraLoiLienHeService {
    constructor( private httpClient: HttpClient ) {}

    Get(idGoc: string): Observable<any> {
        let url = environment.api.url + 'LienHeQuaEmail/getByIDGoc?id=' + idGoc;
        return this.httpClient.get(url);
    }

    add(noiDungLienHe: NoiDungLienHeModelReq): Observable<any> {
        let url = environment.api.url + 'LienHeQuaEmail/add';
        return this.httpClient.post(url, noiDungLienHe);
    }

    GetInfoAdmin(): Observable<any> {
        let url = environment.api.url + 'User/getInfo';
        return this.httpClient.get(url);
    }
}