import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { QuanLyNoiDungLienHeService } from './quan-ly-noi-dung-lien-he.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import {
  NoiDungLienHeModel,
  NoiDungLienHeModelReq,
} from 'src/app/admin/models/noi-dung-lien-he.model';
import { TraLoiLienHeComponent } from './tra-loi-lien-he/tra-loi-lien-he.component';
import { environment, eMenuAdmin } from 'src/environments/environment';
import { MainAdminComponent } from '../main-nav.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-quan-ly-noi-dung-lien-he',
  templateUrl: './quan-ly-noi-dung-lien-he.component.html',
  styleUrls: ['./quan-ly-noi-dung-lien-he.component.scss'],
})
export class QuanLyNoiDungLienHeComponent implements OnInit {
  displayedColumns = ['Name', 'Message', 'NgayCapNhat', 'actions'];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    private __service: QuanLyNoiDungLienHeService,
    public dialog: MatDialog,
    private __snackbar: MatSnackBar,
    private __router: Router,
    private __mainComponent: MainAdminComponent
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.ADMIN,
      eMenuAdmin[eMenuAdmin.QUAN_LY_NOI_DUNG_LIEN_HE]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.ADMIN
    );
  }

  showProgressBar = true;
  dataSource: MatTableDataSource<NoiDungLienHeModel>;
  _length = 0;
  _noiDungLienHes: NoiDungLienHeModel[];
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  private getDataNoiDungLienHe() {
    this.showProgressBar = true;
    this.__service.GetAll().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._noiDungLienHes = data;
        this.dataSource = new MatTableDataSource(this._noiDungLienHes);
        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.dataSource.filterPredicate = (
          data: NoiDungLienHeModel,
          filter: string
        ) =>
          data.Name.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.Email.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.Message.toLocaleLowerCase().indexOf(filter) != -1;

      },

      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }

  ngOnInit(): void {
    this.getDataNoiDungLienHe();
  }

  Reply(noiDungLienHe: NoiDungLienHeModel) {
    var noiDungLienHeReq = new NoiDungLienHeModelReq(noiDungLienHe);
    const req = JSON.parse(JSON.stringify(noiDungLienHeReq));
    const dialogRef = this.dialog.open(TraLoiLienHeComponent, {
      disableClose: true,
      width: '75%',
      height: '98%',
      maxHeight: '98%',
      maxWidth: '75%',
      data: {
        noiDungLienHe: noiDungLienHe,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      this.getDataNoiDungLienHe();
    });
  }
  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
