import {
  Component,
  OnInit,
  Inject,
  Injectable,
  ViewChild,
} from '@angular/core';
import {
  NoiDungLienHeModel,
  NoiDungLienHeModelReq,
} from '../../../../models/noi-dung-lien-he.model';
import {
  InfoAdminModel,
  InfoAdminModelReq,
} from '../../../../models/info-admin.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { TraLoiLienHeService } from './tra-loi-lien-he.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { AlertDialogComponent } from 'src/app/shared/pages/alert-dialog/alert-dialog.component';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-tra-loi-lien-he',
  templateUrl: './tra-loi-lien-he.component.html',
  styleUrls: ['./tra-loi-lien-he.component.scss'],
})
export class TraLoiLienHeComponent implements OnInit {
  _noiDungLienHe = new NoiDungLienHeModelReq(new NoiDungLienHeModel(null));
  _infoAdmin = new InfoAdminModelReq(new InfoAdminModel(null));

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private __service: TraLoiLienHeService,
    private __snackbar: MatSnackBar,
    private __router: Router,
    private __dialog: MatDialog,
    public dialogMainRef: MatDialogRef<TraLoiLienHeComponent>
  ) {}

  dataSource: MatTableDataSource<NoiDungLienHeModel>;
  _noiDungPhanHoies: NoiDungLienHeModel[];
  _length = 0;
  _titleButton: string;

  showProgressBar = false;
  _Name = this.data.noiDungLienHe.Name;
  _Email = this.data.noiDungLienHe.Email;
  _NguoiCapNhat = this.data.noiDungLienHe.NguoiCapNhat;
  _NgayCapNhat = this.data.noiDungLienHe.NgayCapNhat;
  _Message = this.data.noiDungLienHe.Message;
  _id = this.data.noiDungLienHe.id;

  private getDataNoiDungLienHe() {
    this.showProgressBar = true;

    this.__service.Get(this._id).subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._noiDungPhanHoies = data;
        this._noiDungPhanHoies.push(this.data.noiDungLienHe);
        this._noiDungPhanHoies.sort((s1, s2) => {
          if (s1.NgayCapNhat >= s2.NgayCapNhat) return 1;
          else return -1;
        });
      },

      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  private getInfoAdmin() {
    this.showProgressBar = true;

    this.__service.GetInfoAdmin().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._infoAdmin = data;
      },

      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }

  ngOnInit(): void {
    this.getDataNoiDungLienHe();
    this.getInfoAdmin();
    this._noiDungLienHe.Name = '';
    this._noiDungLienHe.Email = '';
    this._noiDungLienHe.Message = '';
    this._titleButton = 'Phản hồi';
  }

  add() {
    this._noiDungLienHe.idGoc = this._id;
    this._noiDungLienHe.Name = this._infoAdmin.DisplayName;
    this._noiDungLienHe.Email = this._infoAdmin.Email;

    if (!this._noiDungLienHe.Message) {
      this.__snackbar.open('Không được bỏ trống ', null, {
        duration: 2000,
      });
    } else {
      this.showProgressBar = true;
      this._noiDungLienHe.Name = this._infoAdmin.DisplayName;
      this._noiDungLienHe.Email = this._infoAdmin.Email;

      this.__service.add(this._noiDungLienHe).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.__snackbar.open('Gửi phản hồi thành công', null, {
              duration: 2000,
            });
            this.getDataNoiDungLienHe();
            this._noiDungLienHe.Message = '';
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
        },

        (error) => {
          this.showProgressBar = false;
          this.showLogin();
          this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
            duration: 2000,
          });
        }
      );
    }
  }
}
